
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import ChinaAddressData from 'vux/src/datas/china_address.json'
import XAddress from 'vux/src/components/x-address/index.vue'

let heights = []
let weights = []
for (var i = 150; i <= 200; i++) {
heights.push({
name: i,
value: i
})
}
for (var k = 40; k <= 100; k++) {
weights.push({
name: k,
value: k
})
}
export default {
components: {
Group,
XInput,
PopupPicker,
XAddress,
Datetime
},
data () {
return {
nickname: '',
sex: ['1'],
birth: '',
sexs: [[{
name: '男',
value: '1'
}, {
name: '女',
value: '2'
}]],
heights: [heights],
weights: [weights],
height: [],
weight: [],
addressData: ChinaAddressData,
adsValue: ['330000', '330200'],
desc: ''
}
}
}



